.s-prices {
	color: #fff;
	padding: 50px 0;

	.container {
		max-width: 1172px; } }

.prices {
	background: $mainBg;
	font-size: 16px;
	position: relative;

	#price-countdown {
		display: none; }

	@include sm {
		margin: 0 -20px; }

	&__tip {
		@include bold;
		text-align: center;
		font-size: 14px;
		color: $themeColor; }

	&__list {
		position: relative;
		padding: 30px 0 30px;
		margin: 0;
		list-style: none;
		display: flex;

		&::-webkit-scrollbar {
			width: 16px;
			height: 16px; }

		&::-webkit-scrollbar-button {
			width: 0px;
			height: 0px; }

		&::-webkit-scrollbar-thumb {
			background: #333;
			border: 0px solid #fff;
			border-radius: 0px;
			cursor: pointer; }

		&::-webkit-scrollbar-thumb:hover {
			background: #333; }

		&::-webkit-scrollbar-thumb:active {
			background: #000; }

		&::-webkit-scrollbar-track {
			background: #e1e1e1;
			border: 0px solid #fff;
			border-radius: 0px; }

		&::-webkit-scrollbar-track:hover {
			background: #e1e1e1; }

		&::-webkit-scrollbar-track:active {
			background: #333; }

		&::-webkit-scrollbar-corner {
			background: transparent; }

		@include tablet;

		@include sm {
			padding: 30px 0 10px; } }

	&__glow {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		width: 50px;
		background: linear-gradient(to right, transparent, #000);
		z-index: 1;
		opacity: 1;
		transition: opacity ease 0.2s;
		pointer-events: none;
		display: none; }

	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		text-align: center;
		padding: 40px 20px 40px;
		box-sizing: border-box;
		height: auto;

		@include sm {
			min-width: 290px; }

		// &:nth-child(2)
		//ackground: $black

		&:not(:last-child) {
			&:after {
				content: '';
				position: absolute;
				right: 0;
				top: 0;
				bottom: 0;
				width: 1px;
				background: linear-gradient(0deg, rgba(187,255,0,0) 0%, rgba(187,255,0,1) 50%, rgba(187,255,0,0) 100%);
				opacity: 0.3; } } }

	&__title {
		@include bold;
		display: flex;
		flex-direction: column;
		font-size: clamp(18px, 2vw, 24px);
		line-height: 1.28;
		margin: 0 0 32px;
		justify-content: center;
		color: $themeColor;
		text-transform: uppercase;

		em {
			font-style: normal;
			color: #FE5D02; }
		@include sm {
			margin: 0 0 30px; } }

	&__price {
		@include bold;
		font-weight: 400;
		color: $themeColor;
		margin: 0 0 16px;
		font-size: 12px;

		h4 {
			@include regular;
			font-size: 60px;
			line-height: 1;
			margin: 12px 0 12px; }

		strong {
			@include bold;
			font-size: clamp(52px, 6vw, 76px);
			color: $themeColor;
			margin: 0 0 20px;
			line-height: 1;
			display: block;

			&:before {
				content: '';
				display: inline-block;
				position: relative;
				margin: 0 5px 0 0; } } }

	&__price-tip {
		@include bold;
		// display: flex
		font-weight: 500;
		font-size: 12px;
		color: $white;
		order: -1;
		margin-bottom: 10px;

		a {
			color: $themeColor; } }

	&__content {
		display: flex;
		height: 100%;
		padding: 0 30px;
		flex-direction: column;

		@include md {
			padding: 0; } }

	&__desc {
		font-size: 12px;
		margin: 45px 0 74px;
		line-height: 1.68;
		text-align: left;

		p {
			&:not(:first-child) {
				margin: 15px 0 0;

				@include sm {
					margin: 12px 0 0; } } }

		@include sm {
			margin: 15px 0 40px; }

		a {
			color: $themeColor; } }

	&__btn {
		margin: auto auto 0;
		max-width: 180px;
		@extend .btn--border;
		@extend .btn--small; }

	&__bottom {
		font-size: 17px;
		padding: 30px 20px;
		text-align: center;
		background: #0e100f; }

	.prices-slider-prev,
	.prices-slider-next {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 64px;
		height: 64px;
		background: $black;
		position: absolute;
		right: -60px;
		top: 50%;
		transform: translateY(-50%);
		z-index: 2;
		cursor: pointer;
		opacity: 1;

		@include laptop {
			width: 46px;
			height: 46px;
			right: 0px; }


		&.swiper-button-disabled {
			opacity: 0;
			visibility: hidden; }

		.icon {
			width: 12px;
			height: 24px;
			fill: $themeColor; } }



	.prices-slider-prev {
		left: -60px;
		right: auto;

		@include laptop {
			left: 0px; } } }
