.sponsors {
	position: relative;
	padding: 100px 0;
	@include md {
		padding: 40px 0; }
	.container {
		max-width: 1110px;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			height: 1px;
			background: linear-gradient(270deg, rgba(187, 255, 0, 0) 0%, #BBFF00 50%, rgba(187, 255, 0, 0) 100%);
			opacity: 0.3; } }

	.section__title {
		@include bold;
		font-size: clamp(32px, 4vw, 64px);
		margin-bottom: clamp(24px, 3vw, 38px); }
	&__description {
		display: flex;
		flex-direction: column;
		text-align: center;
		max-width: 960px;
		margin: 0 auto 64px;
		gap: 64px;

		@include sm {
			font-size: 14px;
			gap: 32px;
			margin: 0 auto 32px; } }

	&__intro {
		@include medium;
		color: $gray;
		font-size: 20px;
		line-height: 1.36;
		text-align: center;

		@include sm {
			font-size: 16px; } }

	&__offer {
		@include bold;
		font-size: 24px;
		color: $themeColor;

		@include sm {
			font-size: 18px; }

		a {
			color: $themeColor; } } }


.sponsors-block {
	position: relative;
	padding: 18px 28px;

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		clip-path: polygon(calc(100% - 20px) 0, 100% 20px, 100% 100%, 20px 100%, 0 calc(100% - 20px), 0 0);
		background: $themeColor;
		opacity: 0.3;
		z-index: -1; }

	&:after {
		content: '';
		position: absolute;
		left: 1px;
		top: 1px;
		right: 1px;
		bottom: 1px;
		clip-path: polygon(calc(100% - 20px) 0, 100% 20px, 100% 100%, 20px 100%, 0 calc(100% - 20px), 0 0);
		background: $mainBg;
		z-index: 0; }

	&__item {
		text-align: center; }
	& + & {
		margin-top: 24px; }
	&_xl {
		.sponsors-block__item {
			max-width: 50%;
			width: 33.33%;
			padding: 25px 30px;
			@include tablet {
				padding: 20px;
				img {
					max-width: 100%; } }
			@include md {
				width: 50%; }
			@include sm {
				width: 75%;
				max-width: 100%;
				padding: 20px 10px; } } }
	&_lg {
		//.sponsors-block__list
		//argin: 0 -50px
		//include tablet
		//margin: 0 -20px
		//include xs
		//margin: 0 -10px
		.sponsors-block__item {
			padding: 25px 30px;
			max-width: 50%;
			width: 33.33%;
			img {
				max-width: 100%; }
			@include tablet {
				padding: 20px; }
			@include md {
				width: 50%; }

			@include xs {
				padding: 7px; }
 } }			//max-width: 217px
	&_md {
		.sponsors-block__list {
			margin: 0 -50px;
			@include tablet {
				margin: 0 -20px; }
			@include xs {
				margin: 0 -10px; } }
		.sponsors-block__item {
			padding: 25px 30px;
			width: 33.33%;
			img {
				max-width: 100%; }
			@include tablet {
				padding: 20px; }
			@include xs {
				padding: 5px; }
			//max-width: 193px
 } }			//width: 100%
	&_sm {
		.sponsors-block__list {
			margin: 0 -25px; }
		.sponsors-block__item {
			padding: 20px 25px;
			width: 25%;
			img {
				max-width: 100%; }
			@include tablet {
				padding: 20px; }

			@include md {
				width: 33.33%; }
			//+md
			//idth: 50%
			//mg
 } }			//max-width: 100%

	&_xs {
		.sponsors-block__list {
			margin: 0 -15px; }
		.sponsors-block__item {
			width: 20%;
			a {
				padding: 20px 25px; }
			img {
				max-width: 100%; }
			@include tablet {
				a {
					padding: 20px; } }

			@include md {
				width: 25%; }

			@include xs {
				width: 33.3%;
				a {
					padding: 15px 10px; } } } }

	&_auto {
		.sponsors-block__list {
			justify-content: space-between;
			margin: 0 -15px; }
		.sponsors-block__item {
			width: auto;
			a {
				padding: 20px 25px; }
			img {
				max-width: 100%; }
			&:last-child {
				margin: 0 auto 0 0; }
			@include tablet {
				a {
					padding: 20px; } }

			@include sm {
				width: 25%;
				a {
					padding: 15px 10px; } }

			@include xs {
				width: 33.3%;
				a {
					padding: 15px 10px; } } } }
	&__list {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		position: relative;
		z-index: 1; }
	&__item {
		@include tablet {
			display: flex;
			justify-content: center; }
		@include sm {
			max-width: 230px;
			img {
				max-width: 100%; } }
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			transition: 0.3s;

			&:hover {
				opacity: 0.7; }

			img {
				transition: all ease 0.2s;
				transform: translateY(0);
				opacity: 1;
				height: auto; } } }

	&__title {
		@include medium;
		font-size: 14px;
		position: relative;
		z-index: 1;
		@include light;
		color: $themeColor;
		margin-bottom: 20px;
		@include sm {
			margin-bottom: 32px; } } }
