.hero {
	position: relative;
	min-height: 100vh;
	//min-height: calc(var(--vh, 1vh) * 100)
	padding: 30px 0 80px;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	background-color: #1e2220;
	z-index: 1;
	pointer-events: none;

	&.hero_teams {
		position: relative;
		padding-top: 33%;
		min-height: 0;
		background: url(../img/teams.png) center top no-repeat;
		background-size: cover;
		margin-bottom: -20%;
		z-index: -1;

		@include md {
 }			// margin-top: 80px

		&:before {
			display: none; }

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(180deg, rgba(18, 18, 18, 0) 0%, $mainBg 90%); } }


	&.hero_inner {
		min-height: 70vh; }

	&.hero_sm {
		min-height: 100px;
		.hero__inner {
			padding: 0; }
		.hero__logo {
			margin-bottom: 60px; }
		.hero__title {
			margin-bottom: 0; } }


	// &:after
	// 	content: ''
	// 	+coverdiv
	// 	z-index: 1
	// 	background-color: #000
	// 	opacity: 0.35

	// &:before
	// 	content: ''
	// 	+coverdiv
	// 	z-index: 1
	// 	background-image: url(../img/hero-overlay.png)
	// 	background-position: center
	// 	background-repeat: no-repeat
	// 	background-size: cover

	@include tablet {
		padding: 45px 0 105px; }
	@include md {
		padding: 60px 0 80px; }
	@include sm {
		padding: 20px 0 60px; }
	@include r(380) {
		@include rhmin(600) {
			padding: 20px 0 40px!important;
			.hero__logo, .h-list {
				margin-bottom: 25px; } } }
	&__video {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: 1;
		pointer-events: none;
		filter: grayscale(100%) brightness(0.8); }

	&__video2 {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: 1;
		pointer-events: none;
		opacity: 0.15; }

	&__bg {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: 2;
		background-size: cover;
		background: url(https://becky.dev/assets/images/whitenoise-e05ce92bb85e9688f3fd742c6e4eb6bf.png);
		animation: 0.09s infinite static;
		opacity: 0.1; }

	&:before {
		content: '';
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		position: absolute;
		background-image: linear-gradient( 0deg, rgba(0, 0, 0, 0.5) 25%, transparent 25%, transparent 50%, rgba(0, 0, 0, 0.5) 50%, rgba(0, 0, 0, 0.5) 75%, transparent 75%, transparent 100% );
		background-size: 8px 8px;
		z-index: 2;
		opacity: 1;
		pointer-events: none;
		animation: 0.09s infinite static; }

	// &:after
	// 	content: ''
	// 	left: 0
	// 	top: 0
	// 	right: 0
	// 	bottom: 0
	// 	position: absolute
	// 	--one: #0a001a
	// 	--two: #000917
	// 	background-image: linear-gradient(90deg, #000000 25%, var(--one) 25%, var(--one) 31.25%, var(--two) 43.75%, var(--two) 50%, #000000 50%, #000000 75%, var(--one) 75%, var(--one) 76.25%, var(--two) 93.75%, var(--two) 100%)
	// 	background-size: 32px 32px
	// 	animation: 0.01s infinite static
	// 	z-index: 1
	// 	opacity: 0.6
	// 	pointer-events: none

	@keyframes static {
		0%,
		100% {
			background-position: 0 0; }
		10% {
			background-position: -5% -10%; }
		20% {
			background-position: -15% 5%; }
		30% {
			background-position: 7% -25%; }
		40% {
			background-position: 20% 25%; }
		50% {
			background-position: -25% 10%; }
		60% {
			background-position: 15% 5%; }
		70% {
			background-position: 0% 15%; }
		80% {
			background-position: 25% 35%; }
		90% {
			background-position: -10% 10%; } }

	&__inner {
		width: 1566px;
		//margin: 0 auto
		padding: 50px 40px;
		position: relative;
		z-index: 2;
		pointer-events: none;
		@include r(1460) {
			padding: 50px 80px; }
		@include r(1279) {
			padding: 50px 40px; }
		@include sm {
			padding: 30px 20px; }
		@include xs {
			padding: 10px 15px; } }

	&__logo {
		width: 202px;
		//height: 101px
		margin: 0 auto 100px;
		@include tablet {
			margin: 0 auto 50px;
			width: 254px; }
		//height: 91px
		@include md {
			width: 200px;
			//height: 82px
			margin: 0 auto 48px; }
		@include xs {
			margin: 20px auto 26px;
			width: 200px;
			height: auto; }

		@media screen and (max-height: 768px) {
			margin: 0 auto 26px; } }


	&__suptitle {
		@include bold;
		font-size: 20px;
		font-weight: 500;
		margin-bottom: 62px;
		text-align: center;
		text-transform: uppercase;
		@include tablet {
			font-size: 18px;
			margin-bottom: 40px; }
		@include md {
			margin-bottom: 28px; }
		@include xs {
			font-size: 15px;
			margin: 0 -10px 13px; }

		@media screen and (max-height: 768px) {
			margin: 0 auto 30px; } }

	&__title {
		font-size: 64px;
		line-height: 1.32;
		text-align: center;
		margin-bottom: 34px;
		color: $themeColor;
		@include black;
		@include laptop;

		@include tablet {
			//font-size: 6.5vw
			//letter-spacing: .06em
			letter-spacing: 3.1px;
			line-height: 1.11;
			margin-bottom: 18px;
			margin-left: -30px;
			margin-right: -30px; }
		@include md {
			font-size: 5.6vw;
			line-height: 1;
			margin-bottom: 32px; }
		@include sm {
			margin-bottom: 20px;
			margin-left: 0;
			margin-right: 0; }
		@include xs {
			font-size: 30px;
			letter-spacing: 1.27px;
			margin: 0 auto 20px;
			line-height: 1.16;
			max-width: calc(100% - 5px); }

		@media screen and (max-height: 768px) {
			margin: 0 auto 30px; } }

	&__btn {
		//max-width: 283px
		//margin: 0 auto
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		.btn {
			pointer-events: auto;
			&:not(:first-child) {
				margin: 0 0 0 20px; } }

		@include sm {
			flex-direction: column;

			.btn {
				margin: 10px auto 0;

				&:not(:first-child) {
					margin: 10px auto 0; } } } }

	&__partner {
		position: absolute;
		right: 10px;
		top: 0;
		font-size: 11px;
		line-height: 1.4;
		pointer-events: auto;

		@include tablet {
			font-size: 14px;
			right: 20px; }

		@include md {
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;
			right: 0;
			margin: 20px auto; } }

	&__partners {
		display: flex;
		margin-top: 4px;

		@include tablet {
			margin-top: 5px; } }

	&__partner-img {
		display: flex;
		align-items: center;
		justify-content: center;

		&:not(:first-child) {
			margin: 0 0 0 20px;

			@include sm {
				margin: 0 0 0 10px; } }

		img {
			height: 60px;
			width: auto; } }

	&__timer {
		position: absolute;
		padding: 0 1vw;
		right: 0;
		left: 0;
		bottom: 1vw;
		font-size: 13vw;
		color: $themeColor;
		opacity: .2;
		line-height: 1;
		white-space: nowrap;
		z-index: 2;
		display: none;
		@include black;

		.live {
			text-align: center; } }

	&_live {
		align-items: flex-start;
		height: 150px;
		min-height: 0;
		padding: 20px 0;

		@include tablet {
			padding: 20px 0; }
		@include md {
			padding: 20px 0;
			height: 120px; }
		@include sm {
			padding: 10px 0; }
		@include xs {
			height: auto;
			padding: 10px 0 !important; }


		.container {
			position: relative;
			z-index: 2;
			pointer-events: auto;
			width: 100%;
			height: 100%;
			padding: 0 80px;

			@include sm {
				padding: 0 60px; } }

		.hero {
			&__bg {
				z-index: -1;
				background: url(../img/screens-done-op.svg) center top no-repeat;
				background-size: cover; }

			&__inner {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 0;
				margin: 0;
				display: flex;
				justify-content: space-between;

				@include md {
					margin: 0; }

				@include xs {
					flex-direction: column-reverse;
					align-items: center; } }

			&__date {
				font-size: 18px;
				margin: auto 0;

				@include md {
					font-size: 14px; }

				@include xs {
					font-size: 12px;
					margin: 15px 0 10px; } }

			&__logo {
				width: 240px;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				margin: 0;

				@include md {
					width: 160px;
					margin: 0px auto 0; }

				@include xs {
					position: relative;
					left: 0;
					top: 0;
					transform: translate(0, 0);
					width: 180px;
					margin: 10px auto 0; } }

			&__partner {
				position: relative;
				height: auto;
				margin: auto 0 auto auto;
				top: 0;
				right: 0;

				@include tablet {
					font-size: 10px; }

				@include md {
					margin: auto 0 auto;

					img {
						height: 30px; } }

				@include xs {
					display: none; }

				img {
					height: 50px; } }

			&__partners {
				@include md {
					margin: 0; } }



			&__partner-text {
				@include sm {
					display: none; } } } } }


.h-list {
	display: flex;
	justify-content: center;
	max-width: 1200px;
	margin: 0 auto 64px;
	padding: 0;

	&_centered {
		justify-content: center; }

	&_min {
		max-width: 411px; }
	@include tablet {
		margin: 0 auto 52px; }
	@include md {
		max-width: 600px;
		margin-bottom: 42px;
		padding: 0; }
	@include xs {
		flex-wrap: wrap;
		justify-content: center;
		padding: 0;
		margin-bottom: 40px; }

	@media screen and (max-height: 768px) {
		margin: 0 auto 40px; }

	&__item {
		display: flex;
		flex-direction: column;

		@include md {
			flex-direction: column;
			text-align: center; }

		@include sm {
			width: 50%;
			align-items: center;
			text-align: center; }
		&:not(:last-child) {
			margin-right: 40px;
			@include sm {
				margin-right: 0; } }
		@include xs {
			margin-bottom: 9px; }
		&:nth-child(2n) {
			@include xs {
				margin-right: 0; } } }
	//+xs
	//	margin-right: 0
	//	//margin-bottom: 20px
	//	margin-bottom: 15px
	//&_sm
	//	@include xs
	//			max-width: 90px
	//&_lg
	//	@include xs
	//			width: auto
	//			max-width: unset

	&__value {
		font-size: 50px;
		margin: 0 20px 0 0;
		@include bold;
		display: flex;
		align-items: center;
		@include md {
			margin: 0;
			justify-content: center; }
		@include sm {
			justify-content: flex-start; }
		@include xs {
			margin-bottom: 0;
			font-size: 32px;
			line-height: 1.1; }

		.icon-infinity {
			width: 71px;
			height: 60px;
			fill: #FFF; } }

	&__text {
		font-size: 14px;
		line-height: 1.4;
		padding: 5px 0;
		@include medium;

		&-top {
			font-size: 14px;
			@include tablet {
				font-size: 16px; }
			@include xs {
				font-size: 14px; } }
		@include xs {
			font-size: 12px; }
		@include tablet {
			padding: 9px 0; }
		@include md {
			padding: 0; } }
	&__icon {
		margin-top: 10px;
		margin-left: 8px;
		font-size: 30px;
		&_anim {
			animation: heart 2s infinite; }
		.icon {
			fill: $white; }
		@include sm {
			margin-top: 6px;
			.icon {
				width: 0.9em; } } } }



@keyframes heart {
	0% {
		transform: scale(1); }
	50% {
		transform: scale(1.2); }
	100% {
		transform: scale(1); } }


.logo {
	display: block;
	//height: 100%
	width: 100%;
	pointer-events: auto;
	img {
		//height: 100%
		width: 100%; } }
