.s-workshops-list {
  padding: clamp(50px, 5vw, 100px) 0;
  position: relative;

  .container {
    max-width: 1280px;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      height: 1px;
      background: linear-gradient(270deg, rgba(187, 255, 0, 0) 0%, #BBFF00 50%, rgba(187, 255, 0, 0) 100%);
      opacity: 0.3; } }

  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: clamp(24px, 4vw, 42px);

    @include sm {
      flex-direction: column; }

    .section {
      &__title {
        text-align: left;
        line-height: 1.33;
        font-weight: 900;
        font-size: clamp(32px, 6vw, 64px);
        margin-bottom: 36px; }

      &__subtitle {
        max-width: none;
        text-align: left;
        line-height: 1.38;
        margin: 30px 0;
        font-weight: 500;
        font-size: 14px;
        color: $gray;
        max-width: 460px;

        a {
          color: $themeColor; }

        @include sm {
          line-height: 1.52; } }

      &__info {
        max-width: none;
        text-align: left;
        line-height: 1.92;
        margin: 24px 0;
        font-weight: 400;
        font-size: 14px;

        p {
          strong {
            @include bold;
            font-size: 16px;
            font-weight: 500;
            color: $themeColor; }

          &:not(:first-child) {
            margin: 4px 0 0; } } } } }

  &__desc {
    max-width: 600px;
    width: 100%;
    flex: 1 1 auto; }

  &__img {
    position: relative;
    max-width: 490px;
    width: 100%;
    flex: 1 1 auto;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 40px;

    @include sm {
      max-width: 520px;
      margin: 0 auto; } } }

