  &__item {
    position: relative;
    aspect-ratio: 1.77;
    z-index: 1;

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block; }

    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background: $themeColor;
      opacity: 0.4; } } }

.workshops-list {
  margin: 0;

  &__item {
    display: flex;
    padding: 26px 0;
    border-top: 1px solid $themeColorD;

    &:last-child {
      border-bottom: 1px solid $themeColorD; }

    @include sm {
      flex-direction: column; } }

  &__item-overlay {
    display: flex;
    width: 100%;
    cursor: pointer;
    text-decoration: none;
    color: #fff;
    @include sm {
      flex-direction: column; }

    &:hover {
      .workshops-list__title {
        text-decoration: underline !important; } } }

  &__type {
    flex: 0 0 60px;
    color: $themeColor;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 500;

    @include sm {
      flex: 1 1 auto;
      margin: 0 0 10px; } }

  &__info {
    flex: 1 1;
    padding-right: 20px; }

  &__title {
    font-size: clamp(18px, 2.6vw, 20px);
    @include bold;
    font-weight: 500;
    line-height: 1.1;
    text-transform: uppercase;
    color: $white; }

  &__desc {
    display: none; }

  &__date {
    font-size: 12px;
    margin: 10px 0 0; }

  &__speakers {
    display: flex;
    flex-direction: column;
    flex: 0 0 180px;
    gap: 5px;
    padding-right: 20px;

    @include sm {
      flex-direction: row;
      flex: 1 1 auto;
      margin: 18px 0;
      gap: 20px 20px; } }

  &__speaker {
    display: flex;
    align-items: center; }

  &__speaker-photo {
    width: 40px;
    height: 40px;
    border-radius: 4px;
    margin: 0 12px 0 0; }

  &__speaker-name {
    font-size: 12px; }

  &__buttons {
    flex: 0 0 150px;
    display: flex;
    flex-direction: column;
    gap: 5px;

    .btn {
      padding: 3px 15px;
      min-height: 40px;
      @extend .btn--border;
      font-size: 14px;

      &:last-child {
        margin-bottom: -10px; } }

    @include sm {
      flex: 1 1 auto;
      flex-direction: row;
      flex: 1 1 auto;
      margin: 0;
      gap: 10px; } } }
